.bodyPages{
   min-height:100%;
}

.titlePages{
   font-style: normal;
   font-weight: 500;
   font-size: 18px;
   line-height: 18px;
   color: #543927;
   margin-bottom:8px;
   margin-top:19px;
}

.out{
   margin-left:4px;
   position:relative;
   top:-1.5px;
   cursor: pointer;
}

.title{
   font-family: 'Alagard';
   font-style: normal;
   font-weight: 500;
   font-size: 39px;
   line-height: 40px;
   text-align: center;
   color: #543927;
}

.NotcomicsCont{
   height:55vh;
   overflow-y:hidden;
   display: flex;
   align-items: center;
   align-content: center;
}

.comicsCont{
   height:100vh;
   overflow-y: scroll;
}

.comics{
   max-width: 100%;
   height: 273px;
}

.readComics{
   width: 110px;
   height: 42px;
   background: #543927;
   border:none;
   margin-top:11px;

   font-style: normal;
   font-weight: 500;
   font-size: 32px;
   line-height: 33px;
   text-align: center;
   color: #E5CDB4;
}
